"use client";
import { useState, useEffect } from "react";

export default function BagClientComponent({ bag }) {
  const [selectedImage, setSelectedImage] = useState(bag?.images?.[0]);
  const [selectedColor, setSelectedColor] = useState(bag?.colors?.[0]);

  // Fallback-safe message generation
  const generateOrderMessage = () => {
    const colorName = selectedColor?.name || bag.colors[0]?.name || "unknown";
    return `Hello! I want to order the "${bag.name}" bag in ${colorName} color. Price: $${bag.price}. Is it still available?`;
  };

  const phone = "963997206837"; // WhatsApp number (international format, no +)
  const rawMessage = generateOrderMessage();
  const encodedMessage = encodeURIComponent(rawMessage);
  const whatsappURL = `https://wa.me/${phone}?text=${encodedMessage}`;

  // Debug output
  useEffect(() => {
    console.log("Raw message:", rawMessage);
    console.log("Encoded WhatsApp URL:", whatsappURL);
  }, [rawMessage, whatsappURL]);

  return (
    <main className="min-h-screen p-10 bg-[#faebf2] relative">
      <div className="max-w-4xl mx-auto flex flex-col items-center justify-center">
        <h1 className="text-3xl font-bold mb-4">{bag.name}</h1>

        {selectedImage && (
          <img
            src={selectedImage}
            alt="Main bag"
            className="w-[500px] rounded-xl mb-6 shadow-md transition duration-300"
          />
        )}

        <p className="text-lg text-gray-700 mb-2">{bag.shortDescription}</p>
        <p className="text-xl font-semibold">${bag.price}</p>
        <p className="text-sm text-gray-500 mt-2">Tag: {bag.tag}</p>

        {/* Available Colors */}
        <div className="mt-6 flex flex-col items-center">
          <h3 className="text-lg font-semibold mb-2">Available Colors:</h3>
          <div className="flex gap-4">
            {bag.colors.map((color, index) => (
              <div
                key={index}
                className="flex items-center gap-2 cursor-pointer"
                onClick={() => setSelectedColor(color)}
              >
                <div
                  className={`w-6 h-6 rounded-full border-2 ${
                    selectedColor?.hex === color.hex ? "border-black" : "border-gray-300"
                  }`}
                  style={{ backgroundColor: color.hex }}
                  title={color.name}
                />
                <span className="text-sm text-gray-700">{color.name}</span>
              </div>
            ))}
          </div>
        </div>

        {/* Thumbnails */}
        <div className="flex gap-4 mt-6">
          {bag.images?.map((img, i) => (
            <img
              key={i}
              src={img}
              alt={`thumb-${i}`}
              onClick={() => setSelectedImage(img)}
              className={`w-20 h-20 rounded-md object-cover cursor-pointer border-2 ${
                selectedImage === img ? "border-black" : "border-transparent"
              }`}
            />
          ))}
        </div>

        {/* WhatsApp Order Button */}
        <a href={whatsappURL} target="_blank" rel="noopener noreferrer">
          <button className="mt-8 bg-[#25D366] hover:bg-[#1ebe57] text-white font-semibold py-2 px-5 rounded-full shadow-md transition">
            ðŸ“² Order via WhatsApp
          </button>
        </a>
      </div>
    </main>
  );
}
    